import { Component } from '@angular/core';
import { Injectable } from '@angular/core';
import { AmazonAuthResponse } from 'angular-amazon-login';
import { AmazonLoginComponent } from 'angular-amazon-login';
import { Amazonservices } from './amazonservices';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
@Injectable()
export class AppComponent {
  title = 'Pillenbox-Konfigurator';

  public showWaitInfo: boolean = false;
  private myClientId: string = 'amzn1.application-oa2-client.a9e64c41743f4d14813be90d8f38cf9f';

  public userName = 'Nicht angemeldet';

  public loggedin: boolean = false;

  constructor(private amService: Amazonservices) {

  }


  onAmazonAuthResponse(event: AmazonAuthResponse) {
    if (event.authRequest.error === undefined) {

      console.log(event);

      this.amService.getUserProfile(event.authRequest.access_token).subscribe(data => {
        console.log(data);
        this.userName = data['name'];
        this.loggedin = true;
      }
        ,
        err => console.log(err)
      );



    }
  }
}




